import React, { useState } from 'react'
import { useNavigate } from "react-router-dom";
import { Tabs, Button, Menu, Carousel, Descriptions, Row, Col, Rate, Divider, Result, Card, Modal } from 'antd';
import { Route, Switch, HashRouter, Link } from 'react-router-dom';
import {
  CloseCircleOutlined,
} from '@ant-design/icons';
function Detail (props) {
  const { language } = props;
  let lan = language;
  console.log(language)
  const [firstname, setfirstname] = useState("");
  const [guanggaostate, setguanggaostate] = useState(true);
  const [list, setlist] = useState([
    {
      'png': 'https://picsum.photos/id/137/200/200',
    }, {
      'png': 'https://picsum.photos/id/448/200/200',
    }, {
      'png': 'https://picsum.photos/id/139/200/200',
    }, {
      'png': 'https://picsum.photos/id/640/200/200',
    }, {
      'png': 'https://picsum.photos/id/537/200/200',
    }, {
      'png': 'https://picsum.photos/id/799/200/200',
    }, {
      'png': 'https://picsum.photos/id/839/200/200',
    }, {
      'png': 'https://picsum.photos/id/110/200/200',
    },
  ])
  const handleOk = () => {
    setguanggaostate(false);
  };

  const handleCancel = () => {
    setguanggaostate(false);
  };
  return (
    <div id="content">
      {guanggaostate === true ?
        <div>
          <Modal title={lan === 'cn' ? '广告' : 'advertising'}
            open={guanggaostate}
            onOk={handleOk}
            onCancel={handleCancel}
            footer={[
              <Button key="back" onClick={handleCancel}>
                {lan === 'cn' ? '返回' : 'back'}
              </Button>,
            ]}
          >
          <amp-ad width="100vw" height="320"
              type="adsense"
              data-ad-client="ca-pub-6251011828646312"
              data-ad-slot="6754370832"
              data-auto-format="rspv"
              data-full-width="">
            <div overflow=""></div>
          </amp-ad>
          </Modal>
          {/* <Button className='fr' type="dashed" icon={<CloseCircleOutlined />} onClick={() => {
            setguanggaostate(false)
          }}>{lan === 'cn' ? '关闭广告' : 'Turn off ads'}</Button> */}

        </div>
        : (
          <div id='mainbox'>
            <Row gutter={[16, 16]}>
              <Col span={12}>
                <Carousel>
                  <div>
                    <img src="https://picsum.photos/id/157/200/200" alt="" />
                  </div>
                  <div>
                    <img src="https://picsum.photos/id/158/200/200" alt="" />
                  </div>
                  <div>
                    <img src="https://picsum.photos/id/258/200/200" alt="" />
                  </div>
                  <div>
                    <img src="https://picsum.photos/id/217/200/200" alt="" />
                  </div>
                </Carousel>
              </Col>
              <Col span={12}>
                <Divider orientation="left">{language === 'en' ? 'Title' : '标题'}</Divider>
                <Rate disabled defaultValue={2} />
                <br />
                <Descriptions
                  title={language === 'en' ? 'Responsive Descriptions' : '描述信息'}
                  bordered
                  column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
                >

                  <Descriptions.Item label="Product">{language === 'en' ? 'Cloud Database' : '云数据库'}</Descriptions.Item>
                  <Descriptions.Item label="Billing">Prepaid</Descriptions.Item>
                  <Descriptions.Item label="time">18:00:00</Descriptions.Item>
                  <Descriptions.Item label="Amount">$80.00</Descriptions.Item>
                  <Descriptions.Item label="Discount">$20.00</Descriptions.Item>
                  <Descriptions.Item label="Official">$60.00</Descriptions.Item>
                  <Descriptions.Item label="Config Info">
                    {language === 'en' ? 'Data disk type' : '数据库类型'}: MongoDB
                    <br />
                    {language === 'en' ? 'Database version' : '数据库版本'}: 3.4
                    <br />
                    {language === 'en' ? 'Package' : '安装包'}: dds.mongo.mid
                    <br />
                    {language === 'en' ? 'Storage space' : '容量'} : 10 GB
                    <br />
                    {language === 'en' ? 'Replication factor' : '复制因子'}: 3
                    <br />
                    {language === 'en' ? 'Region：East China 1' : '地区：中西部'} :
                  </Descriptions.Item>
                </Descriptions>

              </Col>
              <Col span={24}>
                <Result
                  title={language === 'en' ? 'Log in to see details' : '登录后查看详情'}
                  extra={
                    <Button type="primary" key="console">
                      {language === 'en' ? 'to login' : '前往登录'}
                    </Button>
                  } />
              </Col>
            </Row>
            <Divider orientation="left" orientationMargin={50}>{language === 'en' ? 'More' : '更多'}</Divider>
            <Row gutter={[16, 16]}>
              {
                list.map((ele, i) => {
                  return (
                    <Col span={6} key={i}>
                      <Card
                        hoverable
                        cover={<img alt="example" src={ele.png} />}
                      >
                        <Link to='/detail'>{lan === 'cn' ? '显示详情' : 'detail'}</Link>
                      </Card>
                    </Col>
                  )
                })
              }
            </Row>
          </div >
        )
      }


    </div >

  )
}

export default Detail