import React, { useState } from 'react'
import { useNavigate } from "react-router-dom";
import { Tabs, Menu, Carousel, Collapse, Row, Col, Avatar, List, Space, Divider, Button, Checkbox, Form, Input } from 'antd';
import { Route, Switch, HashRouter, Link } from 'react-router-dom';
import { LikeOutlined, MessageOutlined, StarOutlined } from '@ant-design/icons';
import Footers from './../components/footer';
import {
  HomeOutlined,
  OrderedListOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
  SnippetsOutlined
} from '@ant-design/icons';
const { Panel } = Collapse;

function Home (props) {
  console.log(props)
  let lan = props.language;
  const onChange = (key) => {
    console.log(key);
  };
  const data = [
    {
      title: lan === 'en' ? 'Ant Design Title 1' : '标题1',
      content: lan === 'en' ? 'content' : '内容',
    },
    {
      title: lan === 'en' ? 'Ant Design Title 2' : '标题2',
      content: lan === 'en' ? 'content' : '内容',
    },
    {
      title: lan === 'en' ? 'Ant Design Title 3' : '标题3',
      content: lan === 'en' ? 'content' : '内容',
    },
    {
      title: lan === 'en' ? 'Ant Design Title 4' : '标题4',
      content: lan === 'en' ? 'content' : '内容',
    }, {
      title: lan === 'en' ? 'Ant Design Title 5' : '标题5',
      content: lan === 'en' ? 'content' : '内容',
    },
    {
      title: lan === 'en' ? 'Ant Design Title 6' : '标题6',
      content: lan === 'en' ? 'content' : '内容',
    },
  ];
  const text = lan === 'en' ? `
    A dog is a type of domesticated animal.
    Known for its loyalty and faithfulness,
    it can be found as a welcome guest in many households across the world.
  `: `
  狗是一种家养的动物。
它以忠诚和忠诚而闻名，
它在世界各地的许多家庭中都是受欢迎的客人。
  `;
  const tittxt = (index) => {
    return `${lan === 'en' ? `This is panel header` : `这是面板标题`} ${index}`
  }
  const IconText = ({ icon, text }) => (
    <Space>
      {React.createElement(icon)}
      {text}
    </Space>
  );
  const onFinish = (values) => {
    console.log('Success:', values);
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };
  const { TextArea } = Input;
  return (
    <div id="content">
                <amp-ad width="100vw" height="320"
              type="adsense"
              data-ad-client="ca-pub-6251011828646312"
              data-ad-slot="6754370832"
              data-auto-format="rspv"
              data-full-width="">
            <div overflow=""></div>
          </amp-ad>
      <Row gutter={[16, 16]}>
        <Col span={12}>
          <Carousel>
            <div>
              <img src="/images/1.jpg" alt="" />
            </div>
            <div>
              <img src="/images/2.jpg" alt="" />
            </div>
            <div>
              <img src="/images/3.jpg" alt="" />
            </div>
            <div>
              <img src="/images/4.jpg" alt="" />
            </div>
          </Carousel>
        </Col>
        <Col span={12}>
          <Collapse defaultActiveKey={['1']} onChange={onChange}>
            <Panel header={tittxt(1)} key="1">
              <p>{text}</p>
            </Panel>
            <Panel header={tittxt(2)} key="2">
              <p>{text}</p>
            </Panel>
            <Panel header={tittxt(3)} key="3">
              <p>{text}</p>
            </Panel>
          </Collapse>
        </Col>
        <Col span={24}>
          <List
            itemLayout="horizontal"

            dataSource={data}
            renderItem={(item) => (
              <List.Item
                key={item.title}
                actions={[
                  <IconText icon={StarOutlined} text="156" key="list-vertical-star-o" />,
                  <IconText icon={LikeOutlined} text="156" key="list-vertical-like-o" />,
                  <IconText icon={MessageOutlined} text="2" key="list-vertical-message" />,
                ]}>
                <List.Item.Meta
                  avatar={<Avatar src="https://joeschmoe.io/api/v1/random" />}
                  title={<a href="https://ant.design">{item.title}</a>}
                  description="Ant Design, a design language for background applications, is refined by Ant UED Team"
                />
                {item.content}
              </List.Item>
            )}
          />
        </Col>
        <Col span={24}>
          <Divider orientation="center" orientationMargin={50}>
            {lan === 'en' ? `leave a message` : `留言信息`}
          </Divider>
        </Col>
        <Col span={24}>
          <Form
            name="basic"
            labelCol={{
              span: 4,
            }}
            wrapperCol={{
              span: 20,
            }}
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              label={lan === 'en' ? 'title' : '标题'}
              name="username"
              rules={[
                {
                  required: true,
                  message: '请输入内容',
                },
              ]}
            >
              <Input />
            </Form.Item>
            <Form.Item label={lan === 'en' ? 'description' : '描述信息'}
              name="message" rules={[
                {
                  required: true,
                  message: '请输入描述信息',
                },
              ]}>
              <TextArea rows={4} />
            </Form.Item>

            <Form.Item
              wrapperCol={{
                offset: 4,
                span: 16,
              }}
            >
              <Button type="primary" block htmlType="submit">
                {lan === 'en' ? 'submit' : '提交'}
              </Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>


    </div>

  )
}

export default Home