
import { Route, useParams, Switch, HashRouter, Link, useLocation } from 'react-router-dom';
import React, { useState } from "react";
import {
  HomeOutlined,
  OrderedListOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
  SnippetsOutlined
} from '@ant-design/icons';
function Headers (props) {
  let params = useLocation().pathname;//获得地址栏参数
  console.log(useLocation())
  // let data = JSON.parse(params.data);
  // 子向父传值
  const { language } = props;
  let lan = language;
  return (
    <header>
      <div className='topnav'>
        <div className='btn-group'>
          <Link to='/home' className={(params === '/home' || params === '/') ? 'btn active' : 'btn'}><HomeOutlined />{lan === 'cn' ? '首页' : 'home'}</Link>
          <Link className={(params === '/list') ? 'btn active' : 'btn'} to='/list'><OrderedListOutlined />{lan === 'cn' ? '产品列表' : 'list'}</Link>
          <Link className={(params === '/detail') ? 'btn active' : 'btn'} to='/detail'><SnippetsOutlined />{lan === 'cn' ? '产品详情' : 'detail'}</Link>
        </div>
      </div>
    </header>

  )
}

export default Headers